
$screenS:   320px;
$screenS-1: 319px;
$screenM:   576px;
$screenM-1: 575px;
$screenL:   768px;
$screenL-1: 767px;
$screenXL:  1200px;
$screenXL-1:1199px;

$contentWidth: $screenL;


// $colorBackground: #fafafa;
// $colorText: #0b0b0b;
// $colorLinkText: #6d6d6d;
$colorDMBackground: #0b0c0c;
$colorDMBackgroundSecond: #18181c;

$colorLMBackground: #121416;
$colorLMBackgroundSecond: #121416;

$colorText: #f1f1f1;
$colorLinkText: #b5b5b5;
$colorLinkHoverText: $colorText;
$colorLinkHoverBackground: $colorText;


:export {
    // Colors
    
    colorDMBackground: $colorDMBackground;
    colorDMBackgroundSecond: $colorDMBackgroundSecond;

    colorText: $colorText;
    
    colorLinkText: $colorLinkText;
    colorLinkHoverText: $colorLinkHoverText;
    colorLinkHoverBackground: $colorLinkHoverBackground;
    
    // Fonts
    fontPrimary: 'Roboto', sans-serif;
    fontLink: 'Helvetica Neue', sans-serif;
    
    textDecorLink: none;
    

    // Dimensions
    paddingSides: 40px;
    screenS:    $screenS;
    screenS_1:  $screenS-1;
    screenM:    $screenM;
    screenM_1:  $screenM-1;
    screenL:    $screenL;
    screenL_1:  $screenL-1;
    screenXL:   $screenXL;
    screenXL_1: $screenXL-1;

    // Transitions
    transitionLinkHover: all 150ms ease-in-out;

}

